
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

h1 {
  text-align: center;
  margin: 50px;
}

form {
  display: flex;
  justify-content: center;
  height: 30px;
}

form input[type=text] {
  min-width: 250px;
  font-size: 14px;
}
form input[type=submit] {
  font-weight: 700;
  font-size: 18px;
  padding: 0 5px;
}

div.banner-wrapper {
  width: 80%;
  padding: 30px;
  margin: 10px auto;
  text-align: center;
  border-radius: 8px;
  border: 1px solid;
}

.map {
  height: 60vh;
  width: 100%;
  margin-bottom: 100px;
}